body {
    margin: 0;
    padding: 0;
}

.flexNav1 {
    position: relative;
}

.imgLogoNav1 {
    position: absolute;
    top: 15px;
    width: 400px;
    height: auto;
    margin-left: 100px;
    
}

.buttonNav1 {
    position: absolute;
    right: 0;
    background-color: transparent;
    background-image: url("../img/bouton.png");
    font-family: 'jmh_horrorhorror';
    font-size: 1.5em;
    background-repeat: no-repeat;
    border: none;
    margin-right: 100px;
    margin-top: 30px;
    width: 200px;
    height: 95px;
    z-index: 1;
}

.navNav1 {
    height: 80px;
    margin: 0;
    background-color:  #E54C84;
    position: relative;
}


  @media screen and (max-width: 800px) {

    .navNav1 {
    height: 140px;

    }

    .buttonNav1 {

    margin-top: 90px;
    
    }

}

@media screen and (max-width: 420px) {

    .navNav1 {
        height: 180px;
    
        }
    
        .buttonNav1 {
    
        margin-top: 130px;
    
        }
}